import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
import { ProfileProvider } from '../../providers/profile/profile';


@IonicPage()
@Component({
  selector: 'page-event-list',
  templateUrl: 'event-list.html',
})
export class EventListPage {
  public eventList: Array<any>;
  constructor(public navCtrl: NavController, public eventProvider:
    EventProvider, public profileProvider: ProfileProvider) { }


      ionViewDidLoad() {
      this.eventProvider.getEventList().on('value', eventListSnapshot => {
      this.eventList = [];
      eventListSnapshot.forEach(snap => {
        this.eventList.push({
          id: snap.key,
          Nome: snap.val().Nome,
          Preco: snap.val().Preco,
          Data_inicial: snap.val().Data_inicial,
          Data_final: snap.val().Data_final,
          ID_Dono: snap.val().ID_Dono,          
        });
        return false;
      });
    });
  }
  
  goToEventDetail(eventId):void {
    this.navCtrl.push('AnuncioDetalhesPage', { 'eventId': eventId });
    }

}


